import React, { useCallback } from 'react';
import {
  Box, Flex, Text, useClipboard
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';

import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import style from './TransferMoney.module.css';
import { useAppSelector } from '../../../hooks/store';
import Icon from '../../../components/Icon';
import { useDrawer } from '../../../hooks/drawer';

const PullFrom = React.lazy(() => import('../../pullfunds/PullFrom/PullFrom'));
const DepositCheckStart = React.lazy(() => import('../../checkdeposit/DepositCheckStart/DepositCheckStart'));
const MoveFundsIntrabank = React.lazy(() => import('../../pullfunds/MoveFundsIntrabank/MoveFundsIntrabank'));
const MoveFundsAnotherBank = React.lazy(() => import('../../pullfunds/MoveFundsAnotherBank/MoveFundsAnotherBank'));

const TransferMoney = () => {
  const { t } = useTranslation();
  const { open } = useDrawer();
  const { selectedAccount: account } = useAppSelector((store) => store.account, shallowEqual);
  const { hasCopied: hasAccountCopied, onCopy: onAccountCopy } = useClipboard(account?.accountNumber || '', 2000);
  const { hasCopied: hasRoutingCopied, onCopy: onRoutingCopy } = useClipboard(account?.routingNumber || '', 2000);

  const pullFundsFromBank = useCallback(() => {
    open(PullFrom, {
      option: 'bank'
    });
  }, []);

  const pullFundsFromCard = useCallback(() => {
    open(PullFrom, {
      option: 'card'
    });
  }, []);

  const checkDeposit = useCallback(() => {
    open(DepositCheckStart);
  }, []);

  const intrabank = useCallback(() => {
    open(MoveFundsIntrabank);
  }, []);

  const otherBank = useCallback(() => {
    open(MoveFundsAnotherBank);
  }, []);

  return (
    <div className="page" id="transfermoney-page">
      <MiniBox>
        <Box width="100%">
          <Box
            className="fund__push-funds"
            backgroundColor="var(--dashboard-card-background-color)"
            color="var(--dashboard-card-text-color)"
            display="flex"
            minHeight="117px"
            borderRadius="var(--radius)"
            padding="35px 30px 30px 30px"
            margin="20px 0 32px 0"
            justifyContent="space-between"
            flexWrap="wrap"
          >
            <Flex
              width={{ base: '100%', md: 'unset' }}
              className="fund__push-funds__headers"
              display="flex"
              justifyContent="center"
              flexDirection="column"
            >
              <Text className="top-row main-title" paddingBottom="12px" fontSize="24px">
                {t('intraBankFundVC.fund_row_title')}
              </Text>
              <Text
                className="fund__push-funds__headers"
                paddingRight="7px"
                fontSize="14px"
                display="inline-block"
                maxWidth="270px"
                lineHeight="18px"

              >
                {t('move_funds_another_bank.fund_desc')}

              </Text>
            </Flex>
            <Box width={{ base: '100%', md: 'unset' }} className="fund__push-funds__numbers" position="relative">
              <Flex className="top-row" padding="8px 100px 8px 0">
                <Text fontSize="14px" minWidth="134px" fontWeight="600">
                  {t('account_details.accountNumber')}
                </Text>
                <Text fontSize="14px">
                  {account?.accountNumber}
                </Text>
                <Box cursor="pointer" ml="5px" top="7px" right="0" opacity="0.6" transition=".3s">
                  {hasAccountCopied
                    ? <Icon icon="tick" cursor="pointer" size={12} />
                    : <Icon onClick={onAccountCopy} icon="copy" cursor="pointer" size={12} /> }
                </Box>
              </Flex>
              <Flex className="top-row" padding="8px 100px 8px 0">
                <Text fontSize="14px" minWidth="134px" fontWeight="600">
                  {t('account_details.routingNumber')}
                </Text>
                <Text fontSize="14px">
                  {account?.routingNumber}
                </Text>
                {account?.routingNumber !== ''
                && (
                <Box cursor="pointer" ml="5px" top="7px" right="0" opacity="0.6" transition=".3s">
                  {hasRoutingCopied
                    ? <Icon icon="tick" cursor="pointer" size={12} />
                    : <Icon onClick={onRoutingCopy} icon="copy" cursor="pointer" size={12} /> }
                </Box>
                ) }
              </Flex>
              <Flex className="top-row" padding="8px 100px 8px 0">
                <Text fontSize="14px" minWidth="134px" fontWeight="600">
                  {t('account_details.type')}
                </Text>
                <Text fontSize="14px">
                  {account?.type}
                </Text>
              </Flex>
              <Flex className="top-row" padding="8px 100px 8px 0">
                <Text fontSize="14px" minWidth="134px" fontWeight="600">
                  {t('account_details.sponsorBank')}
                </Text>
                <Text fontSize="14px">
                  {account?.sponsorBankName}
                </Text>
              </Flex>
            </Box>
          </Box>

          <Box width={{ base: '100%', md: '48%' }} className={style['fund__move-in']}>
            <Text className="header-1" color="var(--branding)" fontWeight="500" fontSize="24px" lineHeight="26px" paddingBottom="16px">
              {t('move_funds_another_bank.fund_account_tittle')}
            </Text>
            <Box className={style.listWithActionsv2}>
              <ul>
                <li aria-hidden="true" onClick={pullFundsFromBank}>

                  <Flex className={style.details} marginRight="auto">
                    <Text className={style.label} fontSize="16px" lineHeight="20px" variant="regular" textAlign="left">
                      {t('pull_funds_options.fromBankTitle')}
                    </Text>
                    <Text className="value" fontSize="12px" paddingTop="6px" variant="small" textAlign="left">
                      {t('pull_funds_options.fromBankDesc')}
                    </Text>
                  </Flex>
                  <Box className="action">
                    <span className="icon-Arrow-small-right" />
                  </Box>
                </li>
                <li aria-hidden="true" onClick={pullFundsFromCard}>
                  <Flex className={style.details} marginRight="auto">
                    <Text className={style.label} fontSize="16px" lineHeight="20px" variant="regular" textAlign="left">
                      {t('pull_funds_options.fromCardTitle')}
                    </Text>
                    <Text className="value" fontSize="12px" paddingTop="6px" variant="small" textAlign="left">
                      {t('pull_funds_options.fromCardDesc')}
                    </Text>
                  </Flex>
                  <Box className="action">
                    <span className="icon-Arrow-small-right" />
                  </Box>
                </li>
                <li aria-hidden="true" onClick={checkDeposit}>
                  <Flex className={style.details} marginRight="auto">
                    <Text className={style.label} fontSize="16px" lineHeight="20px" variant="regular" textAlign="left">
                      {t('pull_funds_options.depositCheck')}
                    </Text>
                    <Text className="value" fontSize="12px" paddingTop="6px" variant="small" textAlign="left">
                      {t('deposit_check_requirements.RCD_depositCheckDesc')}
                    </Text>
                  </Flex>
                  <Box className="action">
                    <span className="icon-Arrow-small-right" />
                  </Box>
                </li>
              </ul>
            </Box>
          </Box>

          <Box width={{ base: '100%', md: '48%' }} className={style['fund__move-out']}>
            <Text className="header-1" color="var(--branding)" fontWeight="500" fontSize="24px" lineHeight="26px" paddingBottom="16px">
              {t('sendFundVC.move_fund_row_title')}
            </Text>
            <Box className={style.listWithActionsv2}>
              <ul>
                <li aria-hidden="true" onClick={intrabank}>
                  <Flex className={style.details} marginRight="auto">
                    <Text className={style.label} fontSize="16px" lineHeight="20px" variant="regular" textAlign="left">
                      {t('sendFundVC.move_fund_intrabank_title')}
                    </Text>
                    <Text className="value" variant="small" textAlign="left" fontSize="12px" paddingTop="6px">
                      {t('sendFundVC.move_fund_intrabank_desc')}
                    </Text>
                  </Flex>
                  <Box className="action">
                    <span className="icon-Arrow-small-right" />
                  </Box>
                </li>
                <li aria-hidden="true" onClick={otherBank}>
                  <Flex className={style.details} marginRight="auto">
                    <Text className={style.label} fontSize="16px" lineHeight="20px" variant="regular" textAlign="left">
                      {t('sendFundVC.move_fund_anotherBank_title')}

                    </Text>
                    <Text className="value" variant="small" textAlign="left" fontSize="12px" paddingTop="6px">
                      {t('sendFundVC.move_fund_anotherBank_desc')}
                    </Text>
                  </Flex>
                  <Box className="action">
                    <span className="icon-Arrow-small-right" />
                  </Box>
                </li>
              </ul>
            </Box>
          </Box>
        </Box>
      </MiniBox>
    </div>
  );
};

export default TransferMoney;
